.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button{
  padding: 20px;
  font-size: 40px;
  color: white;
  background-color: rgb(120, 108, 14);
}


section h2 {
  font-size: 150px;
  color: pink;
  background: #343ECF;
  background: radial-gradient(ellipse farthest-corner at center center, #343ECF 42%, #FFFFFF 84%, #CFC56A 43%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}